<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">
        首页
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{ goodsmsg.ti_title || "风景名" }}</el-breadcrumb-item>
    </el-breadcrumb>
    <div>
      <el-card>
        <!-- 景点轮播图 -->
        <el-carousel type="card" height="350px">
          <el-carousel-item v-for="(item, i) in goodsmsg.imgList" :key="i">
            <img height="350" :src="item.ttii_url" />
          </el-carousel-item>
        </el-carousel>
        <!-- 景点信息 -->
        <div>
          <h3>介绍</h3>
          <div>
            <p v-html="goodsmsg.ti_context"></p>
            <!-- {{ goodsmsg.ti_context || "内容" }} -->
          </div>
        </div>
        <!-- 评论 -->
        <div class="comment">
          <h3>评论</h3>
          <div style="text-align:center;">
            <el-input type="textarea" v-model="comment"></el-input>
            <el-button style="margin-top:20px;" type="primary" @click="submitcomment">发表评论</el-button>
          </div>
          <!-- 评论列表 -->
          <div class="comment-centet">
            <h3>评论列表</h3>
            <el-card v-for="(item, i) in goodsmsg.commentList" :key="i">
              <div class="top">
                <span>{{ item.user.u_nickname || "用户" }}</span>
                <span
                  >{{ item.tic_createTime || "评论时间" }}
                  <el-button @click.stop="openreply(item)">回复</el-button>
                </span>
              </div>
              <div>
                {{ item.tic_context }}
              </div>
              <div class="reply">
                <reply @getreply="getreply" class="replyitem" v-for="(item1, i1) in item.ic_replyList" :key="i1" :reply="item1"></reply>
              </div>
              <el-dialog title="回复" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
                <el-input type="textarea" v-model="comment"></el-input>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="dialogVisible = false">取 消</el-button>
                  <el-button type="primary" @click="submit">确 定</el-button>
                </span>
              </el-dialog>
            </el-card>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
import reply from "./component/reply"
export default {
  data() {
    return {
      user: window.sessionStorage.user,
      id: this.$route.params.id,
      goodsmsg: {},
      comment: "",
      dialogVisible: false,
      replycontent: "",
      replymsg: {}
    }
  },
  created() {
    this.getgoodsmsg()
  },
  methods: {
    async getgoodsmsg() {
      const { data: res } = await this.$http.get("info/showDetails", { params: { id: this.id } })
      this.goodsmsg = res.obj
      this.goodsmsg.imgList.some(item => {
        item.ttii_url = this.root + item.ttii_url
      })
    },
    openreply(val) {
      this.dialogVisible = true
      this.replymsg = val
    },
    async submit() {
      const { tic_id } = this.replymsg
      const data = {
        tic_id: tic_id,
        ticr_context: this.comment,
        tu_id: JSON.parse(this.user).u_id
      }
      const { data: res } = await this.$http.get("user/addReply", { params: data })
      if (res.code != 200) return
      this.dialogVisible = false
      this.getgoodsmsg()
    },
    // 提交评论
    async submitcomment() {
      const data = {
        ti_id: this.id,
        tu_id: JSON.parse(this.user).u_id,
        tic_context: this.comment
      }
      const { data: res } = await this.$http.get("info/addComment", { params: data })
      if (res.code !== 200) return
      this.$message({
        type: "success",
        message: res.obj
      })
      this.comment = ""
      this.getgoodsmsg()
    },

    getreply(val) {
      this.getgoodsmsg()
    },
    handleClose() {
      this.dialogVisible = false
      this.comment = ""
    }
  },
  components: {
    reply
  }
}
</script>
<style lang="less" scoped>
.el-carousel {
  width: 1080px;
  margin: 0 auto;
}
.comment-centet {
  .top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
}
.reply {
  padding: 0 20px;

  .replyitem {
    padding: 10px;
  }
}
</style>
