<template>
  <div class="reply">
    <div class="top">
      <span>{{ reply.user.u_nickname }}:{{ reply.ticr_context || "内容" }}</span>
      <span
        >{{ reply.ticr_createTime }}
        <el-button @click.stop="openreply">回复</el-button>
      </span>
    </div>
    <div v-if="reply.replyList">
      <reply @getreply="getreply" v-for="(item, i) in reply.replyList" :key="i" :reply="item"></reply>
    </div>
    <el-dialog title="回复" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <el-input type="textarea" v-model="comment"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "reply",
  props: {
    reply: {
      type: Object
    }
  },
  data() {
    return {
      dialogVisible: false,
      comment: "",
      user: window.sessionStorage.user
    }
  },
  methods: {
    openreply() {
      if (!this.user) return this.$router.push("/login")
      this.dialogVisible = true
    },
    async submit() {
      const { tic_id, ticr_id } = this.reply
      const data = {
        tic_id: tic_id,
        ticr_context: this.comment,
        ticr_customer_replyId: ticr_id,
        tu_id: JSON.parse(this.user).u_id
      }
      const { data: res } = await this.$http.get("user/addReply", { params: data })
      if (res.code != 200) return
      this.dialogVisible = false
      this.$emit("getreply", this.comment)
    },
    getreply(val) {
      this.$emit("getreply", val)
    },
    handleClose() {
      this.dialogVisible = false
    }
  }
}
</script>
<style lang="less" scoped>
.reply {
  border-radius: 10px;
  border: 1px solid #eee;
  padding: 10px;
}
.top {
  display: flex;
  justify-content: space-between;
}
</style>
