<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">
        首页
      </el-breadcrumb-item>
      <el-breadcrumb-item v-if="search != ''"> 搜索："{{ search }}" </el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 景点列表 -->
    <div class="scenic-list" v-if="sceniclist">
      <div class="list-item clearfix">
        <ul>
          <router-link :to="'/scenic/details/' + item.ti_id" v-for="item in sceniclist" :key="item.ti_id">
            <li>
              <img :src="item.ti_img.ttii_url || '../assets/img/1.jpg'" width="180" height="180" />
              <h3 class="title">
                {{ item.ti_title || "商品名" }}
              </h3>
              <p class="desc">
                {{ item.ti_context || "商品简介" }}
              </p>
            </li>
          </router-link>
        </ul>
      </div>
    </div>
    <!-- 商品列表 -->
    <div class="goods-list" v-if="goodslist">
      <div class="list-item clearfix">
        <ul>
          <router-link :to="'/goods/details/' + item.tg_id" v-for="item in goodslist" :key="item.tg_id">
            <li>
              <img :src="item.tg_img.ttgi_url || '../assets/img/1.jpg'" width="180" height="180" />
              <h3 class="title">
                {{ item.tg_title || "商品名" }}
              </h3>
              <p class="desc">
                {{ item.tg_context || "商品简介" }}
              </p>
              <div class="prices">
                <span>{{ "￥" + item.tg_price }}</span>
              </div>
            </li>
          </router-link>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      search: this.$route.params.search,
      type: this.$route.params.type,
      sceniclist: [],
      goodslist: [],
      searchlist: []
    }
  },
  created() {
    console.log(this.search, this.type)
    this.getsearchlist()
  },
  methods: {
    async getsearchlist() {
      const { data: res } = await this.$http.get(this.type === "1" ? "info/showAll" : "goods/showAll")
      this.searchlist = res.obj
      this.searchlist.some(item => {
        if (this.type === "1") {
          if (item.ti_title.includes(this.search)) {
            item.ti_img.ttii_url = this.root + item.ti_img.ttii_url
            this.sceniclist.push(item)
          }
        } else {
          if (item.tg_title.includes(this.search) || item.shopper.u_nickname.includes(this.search)) {
            item.tg_img.ttgi_url = this.root + item.tg_img.ttgi_url
            this.goodslist.push(item)
          }
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.scenic-list,
.goods-list {
  margin-top: 40px;
  .list-title {
    margin-bottom: 20px;
    span {
      margin-left: 30px;
      font-size: 24px;
    }
  }
  ul {
    padding: 0;
    list-style: none;
    li {
      float: left;
      padding: 20px 0;
      height: 380px;
      width: 286px;
      background-color: #fff;
      text-align: center;
      transition: all 0.5s;
      img {
        margin: 20px auto 25px;
      }
      .title {
        margin: 20px 10px 5px;
        font-size: 14px;
        font-weight: 400;
        color: #333;
      }
      .desc {
        margin: 10px 20px;
        height: 50px;
        font-size: 12px;
        color: #b0b0b0;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
      }
      .prices {
        color: #fa1d61;
      }
      &:hover {
        transform: translateY(-10px);
        box-shadow: 1px 1px 15px 1px #00000030;
      }
    }
    a:not(:nth-child(4n)) {
      > li {
        margin-right: 15px;
      }
    }
    a:not(:nth-child(-n + 4)) {
      > li {
        margin-top: 15px;
      }
    }
  }
}
.goods-list ul li {
  height: 400px;
}
</style>
