<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">
        首页
      </el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/mall' }">
        商品
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{ goodsmsg.tg_title || "商品名" }}</el-breadcrumb-item>
    </el-breadcrumb>
    <div>
      <el-card>
        <div class="flex-between">
          <!-- 商品轮播图 -->
          <el-carousel type="card" height="350px">
            <el-carousel-item v-for="(item, i) in goodsmsg.tg_imgList" :key="i">
              <img height="350" :src="item.ttgi_url" />
            </el-carousel-item>
          </el-carousel>
          <!-- 商品信息 -->
          <div class="goodsmsg">
            <h4>{{ goodsmsg.tg_title }}</h4>
            <p v-if="goodsmsg.shopper" class="flex-between">
              <span>店主：{{ goodsmsg.shopper.u_nickname }}</span> <span>联系电话：{{ goodsmsg.shopper.u_tel }}</span>
            </p>
            <p>
              <span class="prices">￥{{ goodsmsg.tg_price }}起</span>
            </p>
            <div class="server">
              选择服务：
              <el-checkbox-group v-model="server" @change="serverchange">
                <el-checkbox :label="item.ts_name" v-for="item in goodsmsg.g_serverList" :key="item.ts_id"
                  >{{ item.ts_name }}&nbsp; <span class="prices">￥{{ item.ts_price }}</span>
                </el-checkbox>
              </el-checkbox-group>
            </div>
            <div class="btn">
              <el-button type="danger" @click="open">立即下单</el-button>
            </div>
          </div>
        </div>

        <!-- 商品介绍 -->
        <div>
          <h3>介绍</h3>
          <div>
            <p v-html="goodsmsg.tg_context"></p>
          </div>
        </div>
        <!-- 评价列表 -->
        <div class="comment-centet">
          <h3>评价</h3>
          <h3 class="isnull" v-if="!goodsmsg.post_saleList">暂无评价</h3>
          <div v-else>
            <el-card v-for="(item, i) in goodsmsg.post_saleList" :key="i">
              <div class="top">
                <span>
                  {{ item.customer.u_name || "用户" }}
                  <el-rate v-model="item.tps_score" disabled :colors="colors"> </el-rate>
                </span>
                <span>{{ item.tps_createTime || "评论时间" }}</span>
              </div>
              <div>
                {{ item.tps_commment }}
              </div>
            </el-card>
          </div>
        </div>
      </el-card>
    </div>
    <el-dialog title="下单" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <div>
        <h3>{{ goodsmsg.tg_title }}</h3>
        <div>
          <p>选择服务：{{ serverlist ? serverlist : "无" }}</p>
          <p>总价：{{ prices }}</p>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import reply from "./component/reply"
import dateformat from "../config/dateformat"
export default {
  data() {
    return {
      id: this.$route.params.id,
      dialogVisible: false,
      goodsmsg: {},
      comment: "",
      colors: ["#99A9BF", "#F7BA2A", "#FF9900"],
      server: [],
      serverlist: "",
      prices: 0,
      user: window.sessionStorage.user
    }
  },
  created() {
    this.getgoodsmsg()
  },
  methods: {
    async getgoodsmsg() {
      const { data: res } = await this.$http.get("goods/datails", { params: { id: this.id } })
      this.goodsmsg = res.obj
      this.goodsmsg.tg_imgList.some(item => {
        item.ttgi_url = this.root + item.ttgi_url
      })
      if (this.goodsmsg.post_saleList) {
        this.goodsmsg.post_saleList.some(item => {
          item.tps_createTime = dateformat.dateformat(item.tps_createTime)
        })
      }
      console.log(this.goodsmsg)
    },
    // 打开下单窗口
    open() {
      if (!this.user) return this.$router.push("/login")
      this.dialogVisible = true
      // 计算总价
      this.prices = parseInt(this.goodsmsg.tg_price)
      this.serverlist = ""
      if (this.goodsmsg.g_serverList && this.server.length > 0) {
        this.goodsmsg.g_serverList.some((item, i) => {
          this.server.some((item1, i1) => {
            if (item1 === item.ts_name) {
              this.prices += parseInt(item.ts_price)
            }
            if (i === 0) {
              i1 === 0 ? (this.serverlist += item1) : (this.serverlist += "," + item1)
            }
          })
        })
      }
    },
    async submit(ref) {
      const { tg_id, tg_title: to_title } = this.goodsmsg
      let data = {
        tg_id,
        to_title,
        tu_id: JSON.parse(this.user).u_id,
        to_name: this.serverlist ? this.serverlist : undefined,
        to_price: this.prices
      }
      const { data: res } = await this.$http.get("user/addOrder", { params: data })
      if (res.code !== 200) return
      this.dialogVisible = false
      console.log(res)
    },
    // 选择服务时
    serverchange(val) {
      console.log(val)
    },
    handleClose() {
      this.dialogVisible = false
    }
  },
  components: {
    reply
  }
}
</script>
<style lang="less" scoped>
.goodsmsg {
  width: 400px;
  > p,
  .server,
  .btn {
    margin: 10px 0;
    font-size: 16px;
  }

  .prices {
    color: #fa1d61;
  }
}
.el-carousel {
  width: 750px;
}
.comment-centet {
  .top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  .el-card {
    margin-bottom: 10px;
  }
}
.reply {
  padding: 0 20px;

  .replyitem {
    padding: 10px;
  }
}
.isnull {
  color: #999;
  margin: 20px 0;
  text-align: center;
}
.btn {
  width: 100%;
  text-align: center;
  cursor: default;
  &:active {
    box-shadow: none;
  }
}
</style>
